<template lang='pug'>
  el-container
    el-header
      Header
    el-main
      router-view
    el-footer
      Footer
</template>
<script>
import Header from '@/components/index/Header';
import Footer from '@/components/index/Footer';
export default {
name: '',
 data() {
return {};
},
methods: {},
components: {Header,Footer}
}
</script>
<style scoped lang='stylus'>
  .el-container
    min-height 100%
  .el-header
    padding 0!important
    // z-index 2
  .el-main
    padding 0!important
    // overflow visible!important
    // flex unset
  .el-footer
    padding 0!important
    height 200px!important
</style>



