<template lang='pug'>
  el-row.header
    el-col(:span="2")
      h1 .
    el-col(:span="12")
      el-menu(mode="horizontal" :default-active="$route.path" router class="el-menu-demo" @select="handleSelect" background-color="#545c64" text-color="#fff" active-text-color="#ffd04b")
        el-menu-item(index="/main/index") 首页
        el-submenu(index="2" v-if="userInfo.teacher&&userInfo.name")
          template(slot="title") 我的工作台
          el-menu-item(index="2-1") 发布课程
          el-menu-item(index="2-2") 信息管理
          el-menu-item(index="2-3") 个人中心
        el-submenu(index="3"  v-if="!userInfo.teacher&&userInfo.name")
          template(slot="title") 我的工作台
          el-menu-item(index="3-1") 选择课程
          el-menu-item(index="3-2") 信息管理
          el-menu-item(index="3-3") 个人中心
        el-menu-item(index="/campus/campus") 校园风采
        //- el-menu-item(index="5") 信息中心
        el-menu-item(index="/login/login") 登录
        //- el-menu-item(index="7" v-if="loginState") 个人中心
        el-menu-item(index="/login/register") 注册
        el-submenu(index="0" v-if="userInfo.name")
          template(slot="title") 欢迎您 {{userInfo.name}}
          el-menu-item(index="/login/login" @click="unlogin") 退出登录
    el-col(:span="8") &nbsp;
    el-col(:span="2")
    .logo logo
</template>
<script>
import common from '@/common';
export default {
  name: "Header",
  data() {
    return {
      userInfo:"",
    };
  },
  computed:{
    
  },
  watch:{
   
  },
  mounted() {
       let vm=this;
       this.$nextTick(function () {
        common.Vue.$on('emitGetUserInfo', (userInfo) => {
          vm.init()
        });
         vm.init()
        })
    },
  methods: {
    handleSelect(key, keyPath) {
 
    },
    unlogin(){
          let vm=this;
          sessionStorage.clear();
          localStorage.removeItem('user_info');
          vm.userInfo = {};
          vm.$message({message: "退出成功",type: 'success',duration:2000});
            setTimeout(()=>{
                vm.$router.push({name:"Login"});
                vm.init()
            },2000)
    },
    init(){
      let vm=this;
      let userInfo=JSON.parse(localStorage.getItem('user_info'));
      if(!userInfo){
        vm.userInfo="";
      }else{
         vm.userInfo = userInfo;
      }
    }
  },
  components: {}
};
</script>
<style scoped lang='stylus'>
.header 
  background-color #545c64
  h1
    visibility hidden
  .logo 
    line-height 60px
    color #fff 
    background-color #545c64
    .name
      float right 
      margin-right 30px 


</style>


